import React from "react";
import ReactDOM from "react-dom";

import App from "./components/App";
import reportWebVitals from "./reportWebVitals";
// style
import "./index.css";
import CalciteThemeProvider, {
  CalciteTheme,
} from "calcite-react/CalciteThemeProvider";
import { get } from "./helpers/helpers";

let theme = CalciteTheme;

theme.palette.Brand_Blue_200 = "#1f2574";
theme.palette.blue = "#1f2574";
theme.palette.lightBlue = "#5258A7";
theme.palette.darkBlue = "#5258A7";
console.log(theme);

const init = async () => {
  const config = await get(`${process.env.PUBLIC_URL}/config.json`);

  ReactDOM.render(
    <React.StrictMode>
      <CalciteThemeProvider>
        <App config={config} />
      </CalciteThemeProvider>
    </React.StrictMode>,
    document.getElementById("root")
  );
};

init();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
