import { useAccountManager } from "calcite-react/ArcgisAccount";
import { UserSession } from "@esri/arcgis-rest-auth";

import AppSecured from "./AppSecured";
import Login from "../Login";

const App = ({ config }) => {
  const loginOptions = {
    ...config.authInfo,
    ...{ redirectUri: window.location.href },
  };
  const { accountManagerState, addAccount, logoutAccount } = useAccountManager(
    loginOptions
  );

  const { active, accounts } = accountManagerState;
  const attemptLogin = () => {
    addAccount(loginOptions);
  };
  const attemptLogout = () => {
    logoutAccount(accounts[active]);
  };
  console.log(accountManagerState);

  return active && accounts[active] && accounts[active].session ? (
    <AppSecured
      appConfig={config}
      account={accounts[active]}
      userSession={new UserSession(accounts[active].session)}
      attemptLogout={attemptLogout}
    />
  ) : (
    <Login attemptLogin={attemptLogin} />
  );
};

export default App;
